.App {
  text-align: center;
}

.App-logo {
  height: 30vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 20vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.main-container {
  background-color: #282c34;
  min-height: 80vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.QuizAll button {
  color: black;
  margin: 3px;
  padding: 3px;
  cursor: pointer;
}

.QuizAll h1 {
  color: black;
}

.QuizAll p {
  font-size: 20px;
  color: gray;
}

.QuizAll button :hover {
  opacity: 0.7;
}

.App {
  text-align: center;
}

.board {
  display: grid;
  grid-template-rows: repeat(5, 50px);
  grid-gap: 5px;
  margin: 20px auto;
  width: fit-content;
  border: 1px solid #ccc;
  padding: 10px;
  background-color: #f9f9f9;
}

.row {
  display: grid;
  grid-template-columns: repeat(5, 50px);
  grid-gap: 5px;
}

.tile {
  width: 50px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  border: 1px solid #ccc;
  border-radius: 50%;
}

.red {
  background-color: red;
}

.blue {
  background-color: blue;
}

.green {
  background-color: green;
}

.yellow {
  background-color: yellow;
}

.purple {
  background-color: purple;
}

.selected {
  border: 2px solid black;
}
nav {
  margin: 20px;
}

nav a {
  margin: 0 10px;
  color: #61dafb;
  text-decoration: none;
}

nav a:hover {
  text-decoration: underline;
}
